@mixin placeholder {
  &::-webkit-input-placeholder { @content; }
  &:-moz-placeholder           { @content; }
  &::-moz-placeholder          { @content; }
  &:-ms-input-placeholder      { @content; }
}

.sticker {
    position: relative;
    height: 90vh;
    display: block;
}
.sticker__card {
    position: absolute;
    padding: 7px;
    top: 0;
    left: 0;
    width: 200px;
    background-color: orange;
    color: #fff;
    box-shadow: 3px 3px 2px 0px #949494;
}
.sticker__header {
    display: flex;
    padding-bottom: 7px;
    border-bottom: 1px dashed #fff;
}
.sticker__body {
    padding-top: 20px;
}
.sticker .ready {
    background-color: darkgrey!important;
}
.ready .sticker__body {

    & p {
        text-decoration: line-through;
    }
}
.sticker__move {
    flex-grow: 1;
    cursor: -webkit-grab;
    cursor: grab;
}
.sticker__btn {
    cursor: pointer;
    // padding: 7px;
    background-color: transparent;
    border: none;
    outline: none;

}
.sticker__title {
    font-size: 14px;
    font-weight: bold;
}
input.sticker__title {
    background-color: transparent;
    color: #fff;
    border: none;
    border-bottom: 1px solid #fff;
    outline: none;

    @include placeholder {
        color: rgba(255, 255, 255, 0.58);
    }
}
input.sticker__title,
.sticker__form-group textarea {
    font-weight: normal;
    width: 100%;
}
.sticker textarea {
    color: #000;
    background-attachment: local;
    background-repeat: repeat;
    resize: none;
    outline: none;
    border: 1px solid #fff;

    @include placeholder {
        color: rgba(0, 0, 0, 0.4);
    }
}
.sticker__form-group {
    // padding: 0 7px;
    margin-bottom: 20px;
}
.sticker__footer {
    display: flex;
    justify-content: space-between;
    // padding: 7px;
}
.sticker__link {
    cursor: pointer;
    color: #fff;
    margin-left: 7px;
}
.sticker__checkbox {
    display: block;
    position: relative;
    width: 20px;
    height: 20px;
    background-color: #fff;
    // margin-top: 7px;
    // margin-left: 7px;
    cursor: pointer;

    &::before {
        content:"\e013";
        font-family: 'Glyphicons Halflings';
        display: none;
        position: absolute;
        left: 2px;
        color: #00c108;
    }
}
.ready .sticker__checkbox::before {
    display: block;
}
.sticker__text {
    white-space: pre-line;
}